<template>
    <div>
        <div>
            <h3></h3>
            <div class="divleft">
                <el-input
                        class="padding"
                        v-model="search"
                        placeholder=""
                        prefix-icon="el-icon-search"/>
            </div>
            <div class="divright">
                <el-button @click="handleAdd"></el-button>
            </div>
            <el-table :data="CourseData.filter(data=>!search || data.courseName.includes(search))"
                      class="coursetable"
                      highlight-current-row="true">
                <el-table-column >
                    <el-table-column type="index">
                    </el-table-column>
                    <el-table-column
                            prop="title"
                            label=""
                            min-width="30%"
                            sortable
                    ></el-table-column>
                    <el-table-column
                            prop="description"
                            label=""
                            min-width="40%"
                            sortable
                    ></el-table-column>
<!--                    -->
                    <el-table-column
                            prop="user"
                            label=""
                            min-width="20%"
                            sortable>
                        <template slot-scope="scope">
                            <el-button type="info"
                                       plain
                                       size="small"
                                       @click="showUsers(scope.$index, scope.row)">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                    <!--                    tab-->
                    <el-table-column
                            prop="courseState"
                            label=""
                            min-width="25%"
                            sortable>
                        <template slot-scope="scope">
                            <el-tag :type="scope.row.courseStateTemp">
                                {{scope.row.courseState}}
                            </el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="id"
                            label=""
                            min-width="40%">
                        <template slot-scope="scope">
                            <el-button type="button" size="small" @click="handleEdit(scope.$index, scope.row)">
                                
                            </el-button>
                            <el-button type="button" size="small" @click="handleDel(scope.$index, scope.row)">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
            </el-table>
        </div>
        <!---->
        <el-dialog :title="textMap[dialogStatus]"
                   :visible.sync="dialogFormVisible"
                   :lock-scroll="false"
                   top="5%">
            <!--            -->
            <el-form :model="editForm" label-width="80px" ref="editForm">
                <el-form-item label="">
                    <el-input type="text" v-model="editForm.title"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="editForm.description"></el-input>
                </el-form-item>
                <el-form-item label="" v-if="dialogStatus==='update'">
                    <el-radio-group v-model="editForm.courseState">
<!--                        <el-radio label=""></el-radio>-->
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button @click.native="dialogFormVisible=false"></el-button>
                <el-button v-if="dialogStatus==='create'" type="primary" @click="createData">
                    
                </el-button>
                <el-button v-else type="primary" @click="updateData">
                    
                </el-button>
            </span>
        </el-dialog>
<!--        -->
        <el-dialog :title="''"
                   :visible.sync="UserDialogVisible"
                   :lock-scroll="false"
                   top="5%">
<!--            -->
            <el-table :data="UserForm" height="500px">
                <el-table-column type="index"></el-table-column>
                <el-table-column property="username"
                                 label=""
                                 min-width="20%"
                                 sortable></el-table-column>
                <el-table-column property="state"
                                 label=""
                                 min-width="50%"
                                 sortable>
                    <template slot-scope="scope">
                        <el-radio-group v-model="scope.row.userState">
                            <el-radio label=""></el-radio>
                            <el-radio label=""></el-radio>
                            <el-radio label=""></el-radio>
                        </el-radio-group>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer">
                <el-button @click.native="UserDialogVisible=false"></el-button>
                <el-button type="primary" @click="manageUser"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import getHeader from "../managerRequestHeader.js";

    export default {
        name: "ManageCoursePrototype",

        data(){
            return{
                search:"",

                CourseData: [],

                dialogFormVisible:false,

                UserDialogVisible:false,

                dialogStatus: "",

                textMap: {
                    update: "Edit",
                    create: "Create"
                },

                //
                editForm: {
                    id:"",
                    title:"",
                    description:"",
                    courseState:"",
                    state:"",
                },

                UserForm:[],

                UserEditForm:{
                    username:"",
                    state:"",
                },

                CourseIdtoUser:"",
            }
        },

        methods:{
            showAllCoursePrototypes(){
                var that=this;
                this.$axios.request({
                    url: '/api/coursePrototypes/info/all',
                    method: "get",
                    headers: getHeader.requestHeader()
                })
                    .then(function (response) {
                        console.log(response.data);
                        // alert("");
                        that.CourseData=response.data;

                        // 
                        for (let index=0; index<that.CourseData.length; index++)
                        {
                            var tempState;
                            tempState=response.data[index].state;

                            if (tempState==='NOT_DECIDE') {
                                that.CourseData[index].courseStateTemp="warning";
                                that.CourseData[index].courseState="";
                            }
                            else if (tempState==='USING') {
                                that.CourseData[index].courseStateTemp = 'success';
                                that.CourseData[index].courseState='';
                            }
                            else if (tempState==='DENIAL'){
                                that.CourseData[index].courseStateTemp = 'danger';
                                that.CourseData[index].courseState='';
                            }
                        }


                    })
                    .catch(function (error) {
                        console.log(error.response);
                        alert("");
                    })
            },

            handleDel:function(index,row){
                alert(row.courseName+"");
            },

            //
            handleEdit: function(index, row) {
                this.dialogStatus = "update";
                this.dialogFormVisible = true;
                this.editForm = Object.assign({}, row);
            },

            //
            handleAdd: function() {
                this.dialogStatus = "create";
                this.dialogFormVisible = true;
                this.editForm = {
                    id:"",
                    title:"",
                    description:"",
                    courseState:"",
                }
            },

            // 
            createData(){
                var that=this;

                this.$axios.request({
                    url: '/api/coursePrototypes/',
                    method: "post",
                    headers: getHeader.requestHeader(),
                    data:{
                        title:this.editForm.title,
                        description:this.editForm.description,
                    }
                })
                    .then(function (response) {
                        console.log(response.data);

                        that.showAllCoursePrototypes();
                        that.dialogFormVisible=false;
                    })
                    .catch(function (error) {
                        console.log(error);
                        // alert("");
                    });


            },

            // 
            updateData(){
                var that=this;

                if (this.editForm.courseState===''){
                    this.editForm.state='disapproval';
                }
                else if(this.editForm.courseState===''){
                    this.editForm.state='approval';
                }

                this.$axios.request({
                    url: '/api/coursePrototypes/'+this.editForm.id+'/create',
                    method: "post",
                    headers: getHeader.requestHeader(),
                    params:{
                        decision:this.editForm.state,
                    }
                })
                    .then(function (response) {
                        console.log(response.data);

                        that.showAllCoursePrototypes();
                        that.dialogFormVisible=false;
                        // alert("");
                    })
                    .catch(function (error) {
                        console.log(error);
                        // alert("");
                    });
            },

            // 
            showUsers:function(index,row){
                var that=this;

                that.CourseIdtoUser=row.id;
                that.UserForm=[];

                this.$axios.request({
                    url: '/api/coursePrototypes/'+row.id+'/applications',
                    method: "get",
                    headers: getHeader.requestHeader(),
                })
                    .then(function (response) {
                        console.log(response.data);

                        for (let x=0; x<response.data.length; x++){
                            var res=response.data[x];
                            var userState;

                            if (res.state==='NOT_DECIDE'){
                                userState="";
                            }
                            else if (res.state==='APPROVAL') {
                                userState='';
                            }
                            else if (res.state==='DISAPPROVAL'){
                                userState='';
                            }

                            var tempTeachingAdmin=res.applicationForCoursePK.teachingAdmin;

                            var tempUser={
                                id:tempTeachingAdmin.id,
                                username:tempTeachingAdmin.username,
                                userState:userState,
                                decision:"",
                            };

                            that.UserForm.push(tempUser);
                        }

                        console.log(that.UserForm);
                        // that.showAllCoursePrototypes();
                        // that.dialogFormVisible=false;
                        // alert("");
                    })
                    .catch(function (error) {
                        console.log(error);
                        // alert("");
                    });

                this.UserDialogVisible=true;
            },

            // 
            manageUser:function () {
                var that=this;

                console.log(that.UserForm);

                for (let x=0; x<that.UserForm.length; x++)
                {
                    if (that.UserForm[x].userState===''){
                        that.UserForm[x].decision='disapproval';
                    }
                    else if(that.UserForm[x].userState===''){
                        that.UserForm[x].decision='approval';
                    }

                    this.$axios.request({
                        url: '/api/coursePrototypes/apply',
                        method: "post",
                        headers: getHeader.requestHeader(),
                        params:{
                            applicant_id:this.UserForm[x].id,
                            coursePrototype_id:this.CourseIdtoUser,
                            decision:this.UserForm[x].decision,
                        }
                    })
                        .then(function (response) {
                            console.log(response.data);

                            that.showAllCoursePrototypes();
                            that.UserDialogVisible=false;
                            // alert("");
                        })
                        .catch(function (error) {
                            console.log(error);
                            // alert("");
                        });

                    console.log(that.UserForm[x]);
                }
            },

        },

        mounted() {
            this.showAllCoursePrototypes();
        }
    }
</script>

<style scoped>
    .divleft {
        float: left;
    }

    .divleftmargin{
        float: left;
        margin-right: 20px;
    }

    .divright {
        float: right;
    }

    .padding {
        padding: 8px;
    }

    .coursetable {
        width: 100%;
        font-size: 15px;
        padding-bottom:20px;
    }
</style>
