<template>
    <div>
        <div>
            <h3></h3>
            <div class="divleft">
                <el-input
                        class="padding"
                        v-model="search"
                        placeholder=""
                        prefix-icon="el-icon-search"/>
            </div>
            <div class="divright">
                <el-button @click="handleChooseProto"></el-button>
            </div>
            <el-table :data="CourseData.filter(data=>!search || data.courseTitle.includes(search))"
                      class="coursetable"
                      highlight-current-row="true">
                <el-table-column >
                    <el-table-column type="index">
                    </el-table-column>
                    <el-table-column
                            prop="courseTitle"
                            label=""
                            min-width="30%"
                            sortable
                    ></el-table-column>
                    <el-table-column
                            prop="startDate"
                            label=""
                            min-width="30%"
                            sortable
                    ></el-table-column>
                    <el-table-column
                            prop="endDate"
                            label=""
                            min-width="30%"
                            sortable
                    ></el-table-column>
                    <el-table-column
                            prop="location"
                            label=""
                            min-width="35%"
                            sortable
                    ></el-table-column>
                    <el-table-column
                            prop="courseTeacher"
                            label=""
                            min-width="25%"
                            sortable
                    ></el-table-column>
<!--                    tab-->
                    <el-table-column
                            prop="courseState"
                            label=""
                            min-width="25%"
                            sortable>
                        <template slot-scope="scope">
                            <el-tag :type="scope.row.courseStateTemp">
                                {{scope.row.courseState}}
                            </el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="id"
                            label=""
                            min-width="40%">
                        <template slot-scope="scope">
                            <el-button type="button" size="small" @click="handleEdit(scope.$index, scope.row)">
                                
                            </el-button>
                            <el-button type="button" size="small" @click="handleDel(scope.$index, scope.row)">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table-column>
            </el-table>
        </div>
<!--        -->
        <el-dialog :title="''"
                   :visible.sync="dialogProtoVisible"
                   :lock-scroll="false"
                   top="5%">
            <el-table :data="dialogProtoForm"  height="300px">
                <el-table-column type="index" width="80%">
                    <template slot-scope="scope">
                        <el-radio v-model="dialogProtoChoose" :label="scope.row.id">&nbsp;</el-radio>
                    </template>
                </el-table-column>
                <el-table-column property="title" label="" sortable></el-table-column>
                <el-table-column property="description" label=""></el-table-column>
            </el-table>
            <span slot="footer">
                <el-button @click.native="dialogProtoVisible=false"></el-button>
                <el-button type="primary" @click="handleAdd"></el-button>
            </span>
        </el-dialog>
        <!---->
        <el-dialog :title="textMap[dialogStatus]"
                   :visible.sync="dialogFormVisible"
                   :lock-scroll="false"
                   top="5%">
<!--            -->
            <el-form :model="editForm" label-width="80px" ref="editForm">
                <el-form-item label="">
                    <el-input type="text" v-model="editForm.courseTitle"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-col :span="11">
                        <el-date-picker placeholder=""
                                        type="date"
                                        v-model="editForm.startDate"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        style="width: 100%;">
                        </el-date-picker>
                    </el-col>
                    <el-col class="line" :span="2">-</el-col>
                    <el-col :span="11">
                        <el-date-picker placeholder=""
                                        type="date"
                                        v-model="editForm.endDate"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        style="width: 100%;">
                        </el-date-picker>
                    </el-col>
                </el-form-item>
<!--                -->
                <el-form-item label="">
                    <el-form-item
                            v-for="(timeslot, index) in editForm.timeSlots"
                            :label="''+(index+1)"
                            :key="timeslot.day"
                            :rules="{required: true, message: '', trigger: 'blur'}">
<!--                        <el-input v-model="timeslot.day" placeholder="0-60"></el-input>-->
                        <el-select v-model="timeslot.day" placeholder="">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-time-select
                                v-model="timeslot.start"
                                :picker-options="{start: '08:00',step: '00:30',end: '20:30'}"
                                placeholder="">
                        </el-time-select>
                        <el-time-select
                                v-model="timeslot.end"
                                :picker-options="{start: '08:00',step: '00:30',end: '20:30'}"
                                placeholder="">
                        </el-time-select>
                        <el-button @click.prevent="removeTimeslot(timeslot)"></el-button>
                    </el-form-item>
                </el-form-item>
                <el-form-item>
                    <el-button @click="addTimeslot"></el-button>
                </el-form-item>
                <el-form-item label="">
                    <el-input type="text" v-model="editForm.location"></el-input>
                </el-form-item>
                <el-form-item label="" v-if="dialogStatus==='update'">
                    <el-input type="text" v-model="editForm.courseTeacher"></el-input>
                </el-form-item>
<!--                -->
                <el-form-item label=""
                              v-if="editForm.courseState!=='' && editForm.courseState!=='' && dialogStatus==='update'">
                    <el-radio-group v-model="editForm.courseState">
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
<!--            -->
            <div v-if="dialogStatus==='update'">
<!--                <div class="divleftmargin">-->
<!--                    <el-input type="text"-->
<!--                              placeholder=""-->
<!--                              v-model="addStudent">-->
<!--                    </el-input>-->
<!--                </div>-->
<!--                <div class="divleft">-->
<!--                    <el-button @click="AddCourseStudent">-->
<!--                        -->
<!--                    </el-button>-->
<!--                </div>-->
                <el-table :data="StudentForm" height="300px">
                    <el-table-column type="index"></el-table-column>
                    <el-table-column property="sno" label="" sortable></el-table-column>
                    <el-table-column property="username" label=""></el-table-column>
                    <el-table-column>
                        <template slot-scope="scope">
                            <el-button type="button" @click="DelStudent(scope.$index, scope.row)">
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <span slot="footer">
                <el-button @click.native="dialogFormVisible=false"></el-button>
                <el-button v-if="dialogStatus==='create'" type="primary" @click="createData">
                    
                </el-button>
                <el-button v-else type="primary" @click="updateData">
                    
                </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import getHeader from "../managerRequestHeader.js";

    export default {
        name: "ManageCourse",

        data(){
            return{
                search:"",

                CourseData: [],

                dialogFormVisible:false,

                dialogProtoVisible:false,

                dialogStatus: "",

                textMap: {
                    update: "Edit",
                    create: "Create"
                },

                //
                editForm: {
                    id:0,
                    courseTitle:"",
                    startDate:"",
                    endDate:"",
                    location:"",
                    courseTeacher:"",
                    courseState:"",
                    state:"",
                    timeSlots:[
                        {
                            day:0,
                            end:"",
                            start:"",
                        },
                    ]
                },

                //
                dialogProtoForm:[],

                dialogProtoChoose:"",

                addStudent:"",

                // 
                StudentForm: [],

                options: [{
                    value: '0',
                    label: ''
                }, {
                    value: '1',
                    label: ''
                }, {
                    value: '2',
                    label: ''
                }, {
                    value: '3',
                    label: ''
                }, {
                    value: '4',
                    label: ''
                },{
                    value: '5',
                    label: ''
                }, {
                    value: '6',
                    label: ''
                },
                ],
            }
        },

        methods:{
            showAllCourse(){
                var that=this;
                this.$axios.request({
                        url: '/api/courses/all/info',
                        method: "get",
                        headers: getHeader.requestHeader()
                    })
                    .then(function (response) {
                        console.log(response.data);
                        // alert("");

                        that.CourseData=response.data;

                        // 
                        for (let index=0; index<that.CourseData.length; index++)
                        {
                            var tempState;
                            tempState=response.data[index].state;

                            // 
                            if (response.data[index].teacher!=null)
                            {
                                var tempTeacher;
                                tempTeacher=response.data[index].teacher.username;
                                that.CourseData[index].courseTeacher=tempTeacher;
                            }

                            // 
                            if (tempState==='APPLYING') {
                                that.CourseData[index].courseStateTemp="warning";
                                that.CourseData[index].courseState="";
                            }
                            else if(tempState==='READY_TO_START') {
                                that.CourseData[index].courseStateTemp = 'primary';
                                that.CourseData[index].courseState='';
                            }
                            else if(tempState==='TEACHING'){
                                that.CourseData[index].courseStateTemp = 'success';
                                that.CourseData[index].courseState='';
                            }
                            else if(tempState==='FINISHED'){
                                that.CourseData[index].courseStateTemp = 'info';
                                that.CourseData[index].courseState='';
                            }
                            else if(tempState==='NOT_PASS'){
                                that.CourseData[index].courseStateTemp = 'danger';
                                that.CourseData[index].courseState='';
                            }
                        }

                    })
                    .catch(function (error) {
                        console.log(error.response);
                        alert("");
                    })
            },

            handleDel:function(index,row){
                alert(row.courseName+"");
            },

            //
            handleEdit: function(index, row) {
                this.dialogStatus = "update";
                this.editForm = Object.assign({}, row);
                this.editForm.timeSlots=[
                    {
                        day:0,
                        end:"",
                        start:"",
                    },
                ];

                // 
                var that=this;
                this.$axios.request({
                    url: '/api/courses/'+this.editForm.id+'/students',
                    method: "get",
                    headers: getHeader.requestHeader()
                })
                    .then(function (response) {
                        console.log(response.data);
                        // alert("");
                        that.StudentForm = response.data;
                    })
                    .catch(function (error) {
                        console.log(error.response);
                        // alert("");
                    });

                this.dialogFormVisible = true;
            },

            //
            handleChooseProto(){
                //
                var that=this;
                that.dialogProtoForm=[];

                this.$axios.request({
                    url: '/api/coursePrototypes/info/all',
                    method: "get",
                    headers: getHeader.requestHeader()
                })
                    .then(function (response) {
                        console.log(response.data);

                        for (let x=0; x<response.data.length; x++){
                            if (response.data[x].state==='USING'){
                                that.dialogProtoForm.push(response.data[x]);
                            }
                        }
                        // console.log(that.dialogProtoForm);
                        // that.dialogProtoForm=response.data;
                    })
                    .catch(function (error) {
                        console.log(error.response);
                        alert("");
                    });

                this.dialogProtoVisible=true;
            },

            //
            handleAdd: function() {
                this.dialogProtoVisible=false;
                this.dialogStatus = "create";
                this.editForm = {
                    id:0,
                    courseTitle:"",
                    startDate:"",
                    endDate:"",
                    location:"",
                    courseTeacher:"",
                    courseState:"",
                    state:"",
                    timeSlots:[],
                };
                this.editForm.id=this.dialogProtoChoose;
                // alert(this.editForm.id);
                this.dialogFormVisible = true;
            },

            // 
            createData(){
                var that=this;

                this.$axios.request({
                    url: '/api/courses/start',
                    method: "post",
                    headers: getHeader.requestHeader(),
                    params:{
                        prototypeId:this.editForm.id,
                    },
                    data:{
                        courseTitle:this.editForm.courseTitle,
                        startDate:this.editForm.startDate,
                        endDate:this.editForm.endDate,
                        location:this.editForm.location,
                        timeSlots:this.editForm.timeSlots
                    }
                })
                    .then(function (response) {
                        console.log(response.data);

                        that.showAllCourse();
                        that.dialogFormVisible=false;
                        // alert("");
                    })
                    .catch(function (error) {
                        console.log(error);
                        // alert("");
                    });

                this.dialogFormVisible=false;
            },

            // 
            updateData(){
                var that=this;

                if (this.editForm.courseState===''){
                    this.editForm.state='disapproval';
                }
                else if(this.editForm.courseState===''){
                    this.editForm.state='approval';
                }

                // 
                this.$axios.request({
                    url: '/api/courses/'+this.editForm.id+'/start',
                    method: "post",
                    headers: getHeader.requestHeader(),
                    params:{
                        decision:this.editForm.state,
                    }
                })
                    .then(function (response) {
                        console.log(response.data);

                        that.showAllCourse();
                        that.dialogFormVisible=false;
                        // alert("");
                    })
                    .catch(function (error) {
                        console.log(error);
                        // alert("");
                    });

                // if (this.editForm.timeSlots.day==='MONDAY')
                // {
                //     this.editForm.timeSlots.day='1';
                // }
                //
                this.$axios.request({
                    url: '/api/courses/'+this.editForm.id+'/modify',
                    method: "put",
                    headers: getHeader.requestHeader(),
                    data:{
                        courseTitle:this.editForm.courseTitle,
                        startDate:this.editForm.startDate,
                        endDate:this.editForm.endDate,
                        location:this.editForm.location,
                        timeSlots:this.editForm.timeSlots,
                    }
                })
                    .then(function (response) {
                        console.log(response.data);

                        that.showAllCourse();
                        that.dialogFormVisible=false;
                        alert("");
                    })
                    .catch(function (error) {
                        console.log(error);
                        // alert("");
                    });
            },

            //
            AddCourseStudent(){
                alert(this.addStudent+" "+"");
            },

            //
            DelStudent: function(index, row){
                alert(row.username+"");
            },

            // 
            removeTimeslot(item) {
                var index = this.editForm.timeSlots.indexOf(item);
                if (index !== -1) {
                    this.editForm.timeSlots.splice(index, 1)
                }
            },

            // 
            addTimeslot() {
                this.editForm.timeSlots.push({
                    day:'',
                    start:'',
                    end:'',
                });
            },

        },

        mounted() {
            this.showAllCourse();
        }
    }
</script>

<style>
    .divleft {
        float: left;
    }

    .divleftmargin{
        float: left;
        margin-right: 20px;
    }

    .divright {
        float: right;
    }

    .padding {
        padding: 8px;
    }

    .coursetable {
        width: 100%;
        font-size: 15px;
        padding-bottom:20px;
    }

</style>
