import axios from 'axios'

export default {
    passwordRule: [
        {
            required: true,
            message: "",
            trigger: "change"
        },
        {
            min: 6,
            max: 15,
            message: "615",
            trigger: "blur"
        }
    ],
    telRule: [
        {
            required: true,
            message: "",
            trigger: "change"
        },
        {
            len: 11,
            message: "11",
            trigger: "blur"
        },
        {
            validator: (rule, value, callback) => {
                let temp = ['3','4','5','7','8'];
                if (value[0] === '1' && temp.includes(value[1])) {
                    callback();
                }
                else {
                    callback(new Error(""));
                }
            },
            trigger: "blur"
        },
        {
            validator: (rule, value, callback) => {
                axios.request({
                    url: '/api/users/checkSame/tel',
                    request: 'get',
                    params: {
                        tel: value
                    }
                }).then((response) => {
                    if (response.data) {
                        callback(new Error(""));
                    }
                    else {
                        callback();
                    }
                }).catch((error) => {
                    console.log(error.response);
                })
            },
            trigger: "blur"
        }
    ],
    emailRule: [
        {
            required: true,
            message: "",
            trigger: "change"
        },
        {
            type: "email",
            message: "",
            trigger: "blur"
        },
        {
            validator: (rule, value, callback) => {
                axios.request({
                    url: '/api/users/checkSame/email',
                    request: 'get',
                    params: {
                        email: value
                    }
                }).then((response) => {
                    if (response.data) {
                        callback(new Error(""));
                    }
                    else {
                        callback();
                    }
                }).catch((error) => {
                    console.log(error.response);
                })
            },
            trigger: "blur"
        }
    ],
    requireRule: function (message) {
        return {
            required: true,
            message: message,
            trigger: "change"
        }
    },
    usernameRule: [
        {
            required: true,
            message: "",
            trigger: "change"
        },
        {
            min: 3,
            max: 50,
            message: "350",
            trigger: "change"
        },
        {
            validator: (rule, value, callback) => {
                axios.request({
                    url: '/api/users/checkSame/username',
                    request: 'get',
                    params: {
                        username: value
                    }
                }).then((response) => {
                    if (response.data) {
                        callback(new Error(""));
                    }
                    else {
                        callback();
                    }
                }).catch((error) => {
                    console.log(error.response);
                })
            },
            trigger: "blur"
        }
    ]
}